.who-needs {
  box-sizing: border-box;
  padding: 66px 40px 78px 70px;

  background-image: url("../img/bg-who-needs.svg");
  background-repeat: no-repeat;
  background-position: -107px 18px;
}

.who-needs__heading {
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 38px;

  @include bold(24px, 1.25em, $black);
}

.who-needs__list {
  margin: 0;
  padding: 0;
  padding-right: 32px;

  list-style: none;
}

.who-needs__item {
  box-sizing: border-box;

  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.who-needs__item-heading {
  box-sizing: border-box;
  margin: 0;
  margin-bottom: 10px;

  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;

  @include regular(14px, 1.44em, $blue);
}

.who-needs__item-description {
  box-sizing: border-box;
  margin: 0;

  @include regular(14px, 1.44em, $lightgrey);
}
