@media (max-width: 1440px) {
  @keyframes bubble {
    from {
      transform: scale(1);
    }

    to {
      transform: scale(12);
    }
  }

  @keyframes opacify {
    from {
      color: rgba(47, 128, 237, 0);
    }

    to {
      color: #2f80ed;
    }
  }

  .nav__list {
    position: absolute;
    top: 10px;
    right: 2px;
    z-index: 0;

    display: none;
    padding: 20px 35px 20px 10px;
    overflow: hidden;

    color: #2f80ed;

    border-radius: 10px;
  }

  .nav__list--open {
    animation: opacify;
    animation-duration: 0.15s;
    animation-delay: 0.15s;

    animation-fill-mode: both;
  }

  .nav__list--close {
    animation: opacify;
    animation-duration: 0.15s;
    animation-direction: reverse;

    animation-fill-mode: both;
  }

  .nav__list--visible {
    display: block;
  }

  .nav__list--no-js {
    display: block;

    color: #ffffff;
  }

  .nav__list::before {
    content: "";
    position: absolute;
    top: 6px;
    right: 0;
    z-index: -1;

    display: none;
    box-sizing: border-box;
    width: 42px;
    height: 42px;

    background-color: #ffffff;
    border-radius: 50%;
    transform: scale(12);
  }

  .nav__list--visible::before {
    display: block;
  }

  .nav__list--open::before {
    animation-name: bubble;
    animation-duration: 0.3s;

    animation-fill-mode: both;
  }

  .nav__list--close::before {
    animation-name: bubble;
    animation-duration: 0.3s;
    animation-direction: reverse;

    animation-fill-mode: both;
  }

  .nav__link {
    font-size: 14px;
    line-height: 1.44em;
    font-family: "montserrat-regular", "Arial", sans-serif;
    color: inherit;
  }

  .nav__item:not(:last-child) {
    margin-bottom: 20px;
  }

  .nav__menu-button {
    position: absolute;
    top: 22px;
    right: 8px;

    box-sizing: border-box;
    width: 30px;
    height: 30px;

    background: none;
    background-image: url("../img/icon-menu.svg");
    background-repeat: no-repeat;
    background-position: center;
    border: none;
  }

  .nav__menu-button--close {
    background-image: url("../img/icon-menu-close.svg");
  }

  .nav__menu-button--no-js {
    background: none;
  }
}

